import mglobal from '../js/global'

//
function setSession(k, v) {
  return sessionStorage.setItem(k, v);
}

//
function getSession(k) {
  return sessionStorage.getItem(k);
}

//
function deleteSession(k) {
  return sessionStorage.removeItem(k);
}

//
function deleteSessionAll(k) {
  return sessionStorage.clear();
}

//
function setLocal(k, v) {
  return localStorage.setItem(k, v);
}

//
function getLocal(k) {
  return localStorage.getItem(k);
}

//
function deleteLocal(k) {
  return localStorage.removeItem(k);
}

//qu
function deleteLocalAll() {
  return localStorage.clear();
}

export default {
  //
  setLoginState(state) {
    return setLocal(mglobal.loginState(), state);
  },
  isLogin() {
    return getLocal(mglobal.loginState()) === 'true';
  },
  //
  setUserName(username) {
    return setLocal(mglobal.userName(), username);
  },
  getUserName() {
    return getLocal(mglobal.userName()) === null ? '' : getLocal(mglobal.userName());
  },
  //
  setPwd(pwd) {
    return setLocal(mglobal.pwd(), pwd);
  },
  getPwd() {
    return getLocal(mglobal.pwd()) === null ? '' : getLocal(mglobal.pwd());
  },
  //
  setMobile(mobile) {
    return setLocal(mglobal.mobile(), mobile);
  },
  getMobile() {
    return getLocal(mglobal.mobile()) === null ? '' : getLocal(mglobal.mobile());
  },
  //token
  setToken(token) {
    return setLocal(mglobal.token(), token);
  },
  getToken() {
    return getLocal(mglobal.token()) === null ? '' : getLocal(mglobal.token());
  },
  //15id
  setUserNo(num) {
    return setLocal(mglobal.userNo(), num);
  },
  getUserNo() {
    return getLocal(mglobal.userNo()) === null ? '' : getLocal(mglobal.userNo());
  },
  //
  setUserSeq(seq) {
    return setLocal(mglobal.userSeq(), seq);
  },
  getUserSeq() {
    return getLocal(mglobal.userSeq()) === null ? '' : getLocal(mglobal.userSeq());
  },
  //
  setUserConfirm(isConfirm) {
    return setLocal(mglobal.userConfirm(), isConfirm);
  },
  getUserConfirm() {
    return getLocal(mglobal.userConfirm()) === null ? '' : getLocal(mglobal.userConfirm());
  },
  setInviteNo(no) {
    return setLocal(mglobal.inviteNo(), no);
  },
  getInviteNo() {
    return getLocal(mglobal.inviteNo()) === null ? '' : getLocal(mglobal.inviteNo());
  },

//
  clearUserInfo() {
    deleteLocalAll()
    deleteSessionAll();
  },
}








