<template>
  <div style="height:100%;" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label"></span>
            <Select v-model="operationType" class="input-width">
              <Option v-for="item in statueList" :value="item.value" :key="item.value" class="input-width">{{ item.label }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label"></span>
            <Input v-model="operationContent" placeholder="" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label"></span>
            <Input v-model="operator" placeholder="" class="input-width"></Input>
          </div>
        </Col>
        <Col span="8">
          <div  class="input-item">
            <span class="input-label"></span>
            <DatePicker type="daterange" @on-change="getOperationTime"  placement="bottom-start" placeholder="" class="input-width"></DatePicker>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item">
            <Button type="primary" @click="searchInfo"></Button>
          </div>
        </Col>
      </Row>
    </div>
    <!---->
    <div class="handle-wrap" ref="handleWrap">
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo"></Button>
      </div>
    </div>
    <!--table-->
    <Table :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" ref="table" stripe border></Table>
    <div style="margin-top:10px;height:45px;" ref="pageWrap">
      <div style="float: right;">
        <Page :total="total" :current="page" @on-change="changePage" show-total></Page>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "OperationLogQuery",//
    data () {
      return {
        /* */
        operationContent:'', //
        operator:'',//
        operationTime:'', //
        operationType:'',//
        statueList:[
          {
            label: '',
            value: '11'
          },
          {
            label: '',
            value: '111'
          },
          {
            label: '',
            value: '1111'
          },
        ],
        /*table*/
        tableData: [],
        total: 0,
        page: 1,
        size: 10,//
        tableHeight:200,
        loading:true,
        tableColumns: [
          {
            title: '',
            key: 'jumpway',
            align: 'center',
            sortable: true,
            minWidth: 100,
          },
          {
            title: '',
            align: 'center',
            key: 'jumpway',
            sortable: true,
            minWidth: 100,
          },
          {
            title: '',
            align: 'center',
            key: 'jumpway',
            minWidth: 100,
          },
          {
            title: '',
            key: 'jumpway',
            align: 'center',
            minWidth: 100,
          }
        ]
      }
    },
    mounted() {
      this.tableHeight=this.$refs.tableWrap.clientHeight-50
      this.loadData();
    },
    methods: {
      //
      loadData() {
        var param = new Object();
        param["query_type"] = "Pages";
        param["pPage"] = this.page;
        param["pSize"] = this.size;
        this.$Loading.start();
        this.loading=true;

        let paramData = protocol.getJsonParams(protocol.RecommendMerchant, param);
        http.ajax('', paramData, true, (data) => {

          this.loading=false;
          this.$Loading.finish();
          this.tableData = data.CooperationSeller;
          this.total = data.total;
        })
      },
      //
      changePage (row) {
        this.page = row;
        this.loadData();
      },
      //
      getOperationTime(e){
        this.operationTime=e
        console.log(e)
      },
      //
      searchInfo () {
        //

      },
      //
      exportInfo () {
        var form = document.createElement('form')
        form.enctype = 'application/x-www-form-urlencoded'
        form.method = 'POST'
        form.action = baseUrl + '/pc/information/exportInfo.do'
        var map = {
          'token': that.token,
          'strApp': that.strApp,
          'strInfoNo': that.strInfoNo, //
          'isCash': that.isCash, //     
          'strContent': that.strContent, // 
        }

        for (var i in map) {
          var f = document.createElement('input')
          f.type = 'hidden'
          f.name = i
          f.value = map[i]
          form.appendChild(f)
        }
        document.body.appendChild(form)
        form.submit()
        document.body.removeChild(form)
        form = null
        // this.$refs.table.exportCsv({
        //   filename: ''
        // });
      },
      exportCancel(){

      },
      //
      exportInfo(){
        let that=this
        this.$Modal.confirm({
          title: '',
          content: ``,
          onOk: function(){
            that.exportOk()
          }
        })
      },
    }
  }
</script>

<style lang="less" scoped>
  @import '../../../css/common.css';
</style>





